import React from "react";
import styled from "styled-components";
import vk from "./vk.svg";
import fb from "./fb.svg";
import insta from "./insta.svg";
import twitter from "./twitter.svg";
import viber from "./viber.svg";
import google from "./google.svg";
import appstore from "./appstore.svg";
import windows from "./windows.svg";
const FooterLinksUl = styled.ul`
  padding: 0px;
  display: flex;
  flex-wrap: wrap;
`;
const FooterLinksLI = styled.li`
  list-style: none;
  font-size: 0.75rem;
  color: #5b5b5c;
  margin-right: 15px;
  line-height: 200%;
  > img {
    margin-right: 5px;
  }
`;
const FooterSocs = styled.div`
  text-align: center;

  @media (min-width: 768px) {
    text-align: left;
  }
`;
const SocsP = styled.p`
  font-size: 0.75rem;
  color: #5b5b5c;
`;
const SocStoresWrapper = styled.div`
  flex-direction: column;
  display: flex;
  @media (min-width: 768px) {
    flex-direction: row;
  }
`;
const SocsStores = styled.img`
  padding: 0 0 10px 0px;
  @media (min-width: 768px) {
    padding: 0 10px 0px 0px;
  }
`;

function Footer() {
  return (
    <div className="container">
      <FooterLinksUl>
        <FooterLinksLI> </FooterLinksLI>
        <FooterLinksLI> </FooterLinksLI>
        <FooterLinksLI></FooterLinksLI>
        <FooterLinksLI></FooterLinksLI>
        <FooterLinksLI></FooterLinksLI>
        <FooterLinksLI></FooterLinksLI>
        <FooterLinksLI>White Label </FooterLinksLI>
      </FooterLinksUl>
      <FooterLinksUl>
        <FooterLinksLI>
          <img alt="" src={vk} /> 
        </FooterLinksLI>
        <FooterLinksLI>
          <img alt="" src={fb} />
          
        </FooterLinksLI>
        <FooterLinksLI>
          <img alt="" src={insta} />
          
        </FooterLinksLI>
        <FooterLinksLI>
          <img alt="" src={twitter} />
          
        </FooterLinksLI>
        <FooterLinksLI>
          <img alt="" src={viber} />
          
        </FooterLinksLI>
      </FooterLinksUl>
      <FooterSocs>
        <SocsP>   </SocsP>
        <SocStoresWrapper>
          <SocsStores alt="" src={appstore} />
          <SocsStores alt="" src={google} />
          <SocsStores alt="" src={windows} />
        </SocStoresWrapper>
        <SocsP> 20072018, Aviasales   </SocsP>
      </FooterSocs>
    </div>
  );
}

export default Footer;
