import React from "react";
import styled from "styled-components";
import best from "./best.png";
import BestPricesCard from "./BestPricesCard";
const BestPriesWrapper = styled.div`
  background: #f8fcff;
`;
const BestPricesInfo = styled.div`
  text-align: center;
  padding-top: 40px;

  > img {
    margin: auto auto;
  }
`;
const BestPricesText = styled.div`
  text-align: center;
  padding-bottom: 40px;
  > p {
    line-height: 150%;
  }
`;
const PricesInfo = styled.p`
  color: #4a4a4a;
`;
const PricesRules = styled.p`
  color: #a0b0b9;
`;
function BestPrices() {
  return (
    <BestPriesWrapper>
      <div className="container">
        <div className="row  center-xs">
          <BestPricesInfo>
            <img src={best} alt="" />
            <h3>      </h3>
          </BestPricesInfo>
        </div>
        <div className="row  center-md around-xl padding">
          <BestPricesCard />

          <BestPricesCard />

          <BestPricesCard />
        </div>
        <div className="row center-xs">
          <div className="col-xl-7 col-md-9 ">
            <BestPricesText>
              <PricesInfo>
                 ,    .     220
                 . <br />        100
                  728 .
              </PricesInfo>

              <PricesRules>
                ,     48 , 
                 .
              </PricesRules>
            </BestPricesText>
          </div>
        </div>
      </div>
    </BestPriesWrapper>
  );
}

export default BestPrices;
